.toggle-switch input[type="checkbox"] {
  position: absolute;
  margin-left: -9999px;
  visibility: hidden;
}

.toggle-switch input[type="checkbox"] + label {
  display: block;
  position: relative;
  cursor: pointer;
  outline: none;
  user-select: none;
  padding: 2px;
  width: 50px;
  height: 25px;
  background-color: #dddddd;
  border-radius: 25px;
}

.toggle-switch input[type="checkbox"] + label:before, .toggle-switch input[type="checkbox"] + label:after {
  display: block;
  position: absolute;
  top: 1px;
  left: 1px;
  bottom: 1px;
  content: "";
}

.toggle-switch input[type="checkbox"] + label:before {
  right: 1px;
  background-color: #f1f1f1;
  border-radius: 25px;
  transition: background 0.4s;
}

.toggle-switch input[type="checkbox"] + label:after {
  width: 23px;
  background-color: #fff;
  border-radius: 100%;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  transition: margin 0.4s;
}

.toggle-switch input[type="checkbox"]:checked + label:before {
  background-color: #5cb85c;
}

.toggle-switch input[type="checkbox"]:checked + label:after {
  margin-left: 25px;
}

